.calculator {
    max-width: 1200px;
    margin: 20px auto;
    padding: 20px;
 }
 
 .input-group {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
    gap: 20px;
    margin-bottom: 20px;
 }
 
 .input-field {
    display: flex;
    flex-direction: column;
 }
 
 .input-field label {
    margin-bottom: 5px;
 }
 
 .input-field input {
    padding: 8px;
    border: 1px solid #ccc;
    border-radius: 4px;
 }
 
 .input-field.improvement {
    background-color: #f0f8ff;
    padding: 10px;
    border-radius: 4px;
 }
 
 .input-field.improvement label {
    color: #0056b3;
 }
 
 .impact-section {
    margin: 40px 0;
    padding: 20px;
    background-color: #f0f8ff;
    border-radius: 8px;
 }
 
 .impact-section h2 {
    text-align: center;
    margin-bottom: 20px;
    color: #0056b3;
 }
 
 .impact-inputs {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
    gap: 20px;
 }
 
 .impact-inputs .input-field {
    background-color: white;
    padding: 15px;
    border-radius: 4px;
 }
 
 button {
    background-color: #007bff;
    color: white;
    padding: 10px 20px;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    margin: 20px 0;
 }
 
 button:hover {
    background-color: #0056b3;
 }
 
 .comparison {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 20px;
    margin-top: 20px;
 }
 
 .without, .with {
    padding: 20px;
    border: 1px solid #ccc;
    border-radius: 4px;
 }
 
 .summary {
    margin-top: 20px;
    padding: 20px;
    background-color: #f8f9fa;
    border-radius: 4px;
 }
 
 h1 {
    text-align: center;
    margin-bottom: 30px;
 }
 
 h3 {
    margin-top: 0;
 }